@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&family=Outfit:wght@100..900&display=swap');
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.alpha-wrapper{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    border: 2px solid green;
}


header{
    position: absolute;
    top: 2rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: left;
    gap: 2rem;
    padding-left: 2rem;
    font-family: arial;
    color: whitesmoke;
}

ul{
    display: flex;
    gap: 1rem;
    list-style: none;
}

.full-div{
    width: 100%;
    height: 100%;
}

.top-div{
    height: 67%;
    width: 100%;
    display: flex;
    align-items: center;
    //justify-content: space-between;

    //position: relative;
}

.left-div{
    display: flex;
    width: 55%;
    height: 100%;
    background-repeat: no-repeat;
    //background-size: scale;
    background: url('./../images/desktop-image-hero-1.jpg');
    /*img{
        width: 100%;   
    }*/
}

.right-div-wrapper{
    width: 45%;
    height: 100%;
    position: relative;
}

.right-div{
    padding-top: 3rem;
    padding-left: 3rem;
    padding-right: 10rem;
    width: 100%;
    h1{
        font-weight: 600;
        font-size: 3rem;
        font-family: serif;
        //width: 1rem
        font-family: "Outfit", sans-serif;
        font-optical-sizing: auto;
        font-style: normal;
    }
    p{
        font-weight: 500;
        color: hsl(0, 0%, 63%);
        font-family: "Montserrat", sans-serif;
        font-optical-sizing: auto;
        font-style: normal;
        line-height: 1.5rem;
    }
}

#p{
    color: black;
    letter-spacing: 0.8rem;
}

.bottom-div{
    height: 35%;
    width: 100%;
    display: flex;
}

.right-bottom-left-div{
    position: absolute;
    width: 15rem;
    height: 5rem;
    background: black;

    bottom: 0;
    left: 0;
}